  margin-bottom: 0;
  color: #000;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.fi-dropdown + i {
  display: inline-block;
  color: #000;
  font-size: 2.4rem;
  line-height: 3rem;
  top: 14px;
  z-index: 5;
  transition: all 0.35s ease;
}
.fi-dropdown-menu {
  list-style: none;
  padding: 8px 0;
  margin: 0;
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  max-width: 100%;
  max-height: 185px;
