  border-radius: 4px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(8px);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.08);
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  transition: all 0.25s ease 0.1s;
}
.fi-dropdown-menu::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
  width: 0;
}
.fi-dropdown-item {
  display: block;
  color: #000;
  padding: 12px 24px;
  font-size: 1.6rem;
  line-height: 2.4rem;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
  transition: all 0.3s ease;
}
.fi-dropdown-item:hover {
  background-color: #f4f4f4;
}
.fi-dropdown--show .fi-dropdown-menu {
  opacity: 1;
  visibility: visible;
  transform: none;
}
.fi-dropdown--show i {
  transform: rotateX(180deg);
}
.fi-alert {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  bottom: -26px;
  left: 12px;
  font-weight: 400;
  color: #cf000f;
  text-align: left;
  margin-bottom: 0;
  transform: translateX(-8px);
  transition: all 0.35s ease;
}
.error .fi-alert {
  opacity: 1;
  transform: none;
  visibility: visible;
